.woocommerce .clear::after {
  display: block;
  clear: both;
  content: "";
}
.woocommerce .processing {
  position: relative;
}
.woocommerce .processing::before {
  content: "";
  position: absolute;
  z-index: 50;
  display: block;
  top: 50%;
  left: 50%;
  height: 1.5em;
  width: 1.5em;
  transform: translate3d(-50%, -50%, 0);
  background: url("./../../images/loader-black.svg") center center;
  background-size: cover;
  font-size: 3rem;
  line-height: 1;
  text-align: center;
  color: rgba(0, 0, 0, 0.75);
}
.woocommerce .loader::before {
  content: "";
  position: absolute;
  z-index: 50;
  display: block;
  top: 50%;
  left: 50%;
  height: 1.5em;
  width: 1.5em;
  transform: translate3d(-50%, -50%, 0);
  background: url("./../../images/loader-black.svg") center center;
  background-size: cover;
  font-size: 3rem;
  line-height: 1;
  text-align: center;
  color: rgba(0, 0, 0, 0.75);
}
.woocommerce .fadeIn {
  opacity: 0;
  transition: opacity 1s cubic-bezier(0.6, -0.28, 0.735, 0.045);
}
.woocommerce .fadeIn.animated {
  opacity: 1;
}
.woocommerce .col2-set {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .woocommerce .col2-set {
    grid-template-columns: repeat(2, 1fr);
  }
}
.woocommerce .with-scroll-to-top__scroll-point {
  position: relative;
  top: calc(-1 * var(--wp--header-height, 80px));
}
.woocommerce .components-visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}
.woocommerce p.demo_store,
.woocommerce .woocommerce-store-notice {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0;
  padding: 1rem;
  text-align: center;
  z-index: 99998;
  background-color: white;
  box-shadow: 0 -1px 1rem rgba(0, 0, 0, 0.2);
  display: none;
}
.woocommerce p.demo_store a,
.woocommerce .woocommerce-store-notice a {
  float: right;
  text-decoration: underline;
}
.woocommerce .added_to_cart {
  display: none;
}

/* Layout */
.wc-block-components-sidebar-layout {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  gap: calc(2 * var(--wp--custom--gutter));
}

.wc-block-components-main,
.wc-block-components-sidebar {
  flex: 1 0 100%;
  width: 100%;
}
.is-large .wc-block-components-main,
.is-large .wc-block-components-sidebar {
  flex: 1 0 0%;
}

.is-large .wc-block-components-sidebar {
  position: sticky;
  top: var(--wp--header-height, 80px);
  align-self: flex-start;
  flex: 0 0 30%;
}

/* Snackbar */
.components-snackbar-list {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 5;
}
.components-snackbar-list__notice-container {
  background-color: var(--wp--gray-900);
  color: var(--wp--preset--color--white);
  padding: var(--wp--custom--gutter);
}

/* Drawer */
.wc-block-components-drawer {
  --wp--drawer--width: 100%;
  position: fixed;
  background: var(--wp--preset--color--white);
  display: block;
  height: 100%;
  left: 100%;
  right: 0;
  top: 0;
  transform: translateX(calc(var(--wp--drawer--width) * -1));
  width: var(--wp--drawer--width);
}
@media only screen and (min-width: 480px) {
  .wc-block-components-drawer {
    --wp--drawer--width: 480px;
  }
}
.wc-block-components-drawer__screen-overlay {
  --wp--animation--duration: .3s;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  background-color: rgba(95, 95, 95, 0.35);
  transition: opacity var(--wp--animation--duration);
  opacity: 1;
  z-index: 9999;
}
.wc-block-components-drawer__screen-overlay--with-slide-in {
  animation-duration: var(--wp--animation--duration);
  animation-name: fadeIn;
}
.wc-block-components-drawer__screen-overlay--with-slide-in .wc-block-components-drawer {
  animation-duration: var(--wp--animation--duration);
  animation-name: slideOutLeft;
}
.wc-block-components-drawer__screen-overlay--with-slide-out .wc-block-components-drawer {
  transition: transform var(--wp--animation--duration) ease-in-out;
}
.wc-block-components-drawer__screen-overlay--is-hidden {
  pointer-events: none;
  opacity: 0;
}
.wc-block-components-drawer__screen-overlay--is-hidden .wc-block-components-drawer {
  transform: translateX(0);
}

